.hero {
    display: flex;
    justify-content: center;
    gap: var(--lg-spacing);
    min-height: 85vh;
    height: 100%;
    align-content: center;
    flex-wrap: wrap;
    font-family: 'Merriweather Sans', sans-serif;
    border-bottom: 1px solid var(--gray-3);
    position: relative;
}

.hero img.profile-picture {
    width: 250px;
    height: 250px;
}

.hero div.info-container {
    max-width: 35vw;
    margin-bottom: 2em;
}

.hero h2 {
    font-size: 3em;
    font-weight: 900;
    font-family: 'Nexa', 'Merriweather Sans', sans-serif;
    margin-top: 0;
    justify-content: flex-start;
}

.hero h2 span {
    color: var(--primary-color);
}

.hero p {
    font-size: 1.5em;
    font-weight: 100;
    line-height: 2em;
    color: var(--gray-2);
    font-family: 'Merriweather';
    letter-spacing: 0.02em;
}

.hero p span {
    color: var(--accent-color);
    font-weight: 600;
}

.hero .cta {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: var(--smp-spacing);
    padding: var(--smp-spacing) var(--mdp-spacing);
    position: relative;
    background-color: #ffffff;
    border-radius: var(--lg-spacing);
    border: 1px solid;
    border-color: var(--gray-3);
    position: absolute;
    bottom: -26px;
    z-index: 9;
    cursor: pointer;
}

button.cta:hover {
    background: #f9f9f9;
    color: white;
    transition: 0.3s ease-in;
}

.hero .cta img {
    width: var(--mdp-spacing);
}

.hero .cta p {
    font-family: "Merriweather Sans-Light", Helvetica;
    font-weight: 300;
    color: var(--gray-2);
    font-size: 14px;
    letter-spacing: 2.52px;
    text-transform: uppercase;
    margin: 0;
}

.title.static {
    display: none;
}

.hero .contact .buttons-row a {
    border: 1px solid var(--accent-color);
    padding: 1em;
    color: var(--accent-color);
    border-radius: 6px;
    text-decoration: none;
    box-shadow: 1px 1px 1px 1px var(--accent-color);
    font-family: 'Merriweather';
    margin: 0em 1em 0 0;
}

.hero .contact .buttons-row a:hover {
    background: #e5097d14;
    transition: 0.3s ease;
}

.hero .contact .info-row a {
    text-decoration: none;;
}

.contact {
    width: 100%;
    display: flex;
    flex-flow: column;
    gap: 24px;
}

.info-row {
    display: flex;
    gap: 32px;
}

.info-row img {
    width: 24px;
    /* color: red; */
}

.info-item p {
    font-size: 1em;
    margin: 0;
    font-family: 'Merriweather Sans';
    font-weight: 200;
    color: var(--gray-2);
}

.info-item {
    display: flex;
    gap: 8px;
}

.buttons-row {
    display: flex;
}

@media screen and (max-width: 1366px) {
    .hero div.info-container {
        max-width: 45vw;
    }

    .hero h2 {
        font-size: 2.6em;
    }

    .hero p.paragraph {
        font-size: 1.3em;
    }
}

@media screen and (max-width: 1024px) {
    .hero {
        flex-flow: column;
        margin-top: 72px;
        align-items: center;
    }

    .hero div.info-container {
        max-width: 75vw;
    }

    .hero p.paragraph {
        text-align: center;
        margin-bottom: 48px;
    }

    .hero .contact {
        display: flex;
        margin-bottom: 3em;
    }

    .buttons-row {
        display: flex;
        justify-content: center;
    }

    .info-row {
        display: flex;
        gap: 32px;
        justify-content: center;
    }

    .title.typewriter {
        display: none;
    }

    .title.static {
        display: block;
        text-align: center;
        color: var(--primary-color)
    }

}

@media screen and (max-width: 768px) {
    .info-row {
        flex-flow: column;
        align-items: center;
        gap: 8px;
    }
}