.footer-content {
    display: flex;
    align-items: center;
    justify-content: center;
}

footer.footer {
    display: flex;
    background: var(--gray-2);
    color: white;
    justify-content: center;
}

.footer h2 {
    font-family: 'Nexa';
    font-size: 2em;
    margin: 0em 0;
}

.footer h3 {
    font-family: 'Nexa Light';
    font-size: 2em;
    margin: 0.5em 0;
    font-style: italic;
    color: white;
}

@media screen and (max-width: 768px) {
    .footer-content {
        flex-flow: column;
        margin: 1em 0;
    }

    .footer h3 {
        margin: 0;
        font-size: 1.5em;
    }
}