header.header {
    display: flex;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
    /* Asegura que el menú esté por encima de otros elementos */
    background-color: white;
    /* Opcional: para que el fondo sea visible si hay contenido detrás */
    justify-content: center;
}

.header div.header-container {
    display: flex;
    justify-content: space-between;
    flex-grow: 1;
    padding: var(--md-spacing) var(--md-spacing);
    max-width: 1366px;
}

.header h1 {
    font-family: 'Nexa', sans-serif;
    font-weight: 900;
    font-size: 1.5em;
    color: var(--gray-2);
}

.header h1 span {
    color: var(--primary-color);
}

.header a {
    text-decoration: none;
}