.content p {
    font-size: 1.1em;
    font-weight: 100;
    color: var(--gray-2);
    line-height: 1.8em;
    letter-spacing: 0.02em;
    width: 60%;
}

.content p b {
    color: var(--accent-color);
    font-weight: 700;
}

div#about {
    height: 100vh;
    padding-top: 48px;
}

@media screen and (max-width: 1024px) {
    .content p {
        width: 100%;
    }
}