nav.menu {
    display: flex;
    align-items: center;
}

nav.menu ul {
    display: flex;
    gap: var(--lg-spacing);
    margin: 0;
    padding: 0;
    font-weight: 100;
    color: var(--gray-1);
}

nav.menu li {
    list-style: none;
    font-family: 'Merriweather Sans', sans-serif;
    text-transform: uppercase;
}

/* Animación linea */

.menu-item {
    position: relative;
    cursor: pointer;
    padding: 5px 0;
}

.menu-item a{
    color: inherit;
    text-decoration: none;
    font-weight: 300;
}

.menu-item .menu-link {
    color: inherit;
    text-decoration: none;
    font-weight: 300;
    background: none;
    border: none;
    padding: 0;
    font-family: inherit;
    font-size: inherit;
    text-transform: inherit;
    cursor: pointer;
}

.menu-item:hover, .menu-item.active {
    color: black;
    font-weight: 400;
    transition: 0.15s ease-in-out;
}

.menu-item.active::after {
    content: "";
    position: absolute;
    left: 50%;
    /* Empieza en el centro */
    bottom: 0;
    /* Pegado abajo del texto */
    width: 100%;
    height: 2px;
    background-color: var(--primary-color);
}

.mobile-menu .menu-item:hover::after,
.mobile-menu .menu-item.active::after {
    display: none;
}

.menu-item::after {
    content: "";
    position: absolute;
    left: 50%;
    /* Empieza en el centro */
    bottom: 0;
    /* Pegado abajo del texto */
    width: 0;
    height: 2px;
    background-color: var(--primary-color);
    transition: all 0.15s ease;
    transform: translateX(-50%);
    /* Centra el punto inicial */
}

/* Al hacer hover */
.menu-item:hover::after {
    width: 100%;
    /* Se expande de centro a bordes */
}

.menu-hamburger {
    background: none;
    border: none;
    cursor: pointer;
    margin-left: auto;
    display: flex;
    align-items: center;
    padding: 8px;
    color: var(--gray-1);
    transition: color 0.2s;
    display: none;;
}

.menu-hamburger img {
    width: 24px;
    height: 24px;
}

.menu-hamburger:hover {
    color: var(--primary-color);
}

.mobile-menu-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 2000;
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
}

.mobile-menu {
    background: white;
    border-radius: 8px;
    margin: 16px 16px 0 0;
    box-shadow: 0 4px 24px rgba(0,0,0,0.08);
    padding: 24px 32px;
    min-width: 180px;
    animation: fadeInMobileMenu 0.2s;
}

@keyframes fadeInMobileMenu {
    from { opacity: 0; transform: translateY(-20px); }
    to { opacity: 1; transform: translateY(0); }
}

.mobile-menu ul {
    display: flex;
    flex-direction: column;
    gap: 18px;
    margin: 0;
    padding: 0;
}

.mobile-menu .menu-item {
    font-size: 1.1em;
    padding: 0;
}

@media screen and (max-width: 768px) {
    nav.menu ul.desktop-menu {
        display: none; /* Oculta el menú en pantallas pequeñas */
    }

    .menu-hamburger {
        display: flex; /* Muestra el botón de menú en pantallas pequeñas */
    }
    
    .mobile-menu-overlay {
        display: flex;
    }
}