.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* Nexa Font */
@font-face {
  font-family: 'Nexa';
  src: url('./assets/fonts/nexa/Nexa-Heavy.woff2') format('woff2'),
      url('./assets/fonts/nexa/Nexa-Heavy.woff') format('woff');
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Nexa Extra';
  src: url('./assets/fonts/nexa/Nexa-ExtraLight.woff2') format('woff2'),
      url('./assets/fonts/nexa/Nexa-ExtraLight.woff') format('woff');
  font-weight: 100;
  font-style: normal;
  font-display: swap;
}

/* Base Styles */

h3 {
    font-size: 2.4em;
    font-family: 'Nexa', 'Merriweather Sans';
    font-weight: 100;
    color: var(--gray-2);
}

.base-section {
  display: flex;
  justify-content: center;
  overflow: hidden;
}

.content {
  display: flex;
  width: 50vw;
  flex-flow: column;
}
@media screen and (max-width: 1440px) {
    .content {
        width: 80vw;
    }
    
}

@media screen and (max-width: 1024px) {
    .content {
        width: 100vw;
        padding: 0 2em;
    }
}