.study-case-detail {
    display: flex;
    flex-flow: column;
    width: 100%;
    align-items: center;
}

.study-case-detail .content {
    width: 100%;
}

.study-case-detail .section {
    padding: 2.5em 5em;
    display: flex;
    justify-content: center;
}

.study-case-detail .section.gray-section {
    background: var(--gray-5);
}

.study-case-detail .section .section-content {
    width: 50vw;
}

.study-case-detail .row {
    display: flex;
    flex-flow: row;
    border-bottom: 2px solid var(--gray-4);
    padding: 1em 0;
}

.summary-table .col.title {
    min-width: 150px;
    justify-content: flex-start;
    font-weight: bold;
}

.study-case-detail h1 {
    font-size: 2.4em;
    font-family: 'Nexa', 'Merriweather Sans';
    font-weight: 100;
    color: var(--gray-2);
}

.study-case-detail p {
    width: 100%;
}

.summary-table {
    margin: 1em 0 1em;
    line-height: 2em;
    font-size: 1.1em;
    font-weight: 100;
}

.study-case-detail span {
    text-transform: uppercase;
    letter-spacing: 0.2em;
    font-family: "Merriweather Sans";
    color: var(--gray-1);
    font-weight: 100;
}

.study-case-detail ul {
    padding: 0;
    margin: 0;
    font-weight: 100;
}

.info-grid {
    display: flex;
    justify-content: center;
    ;
}

.info-grid .card {
    font-size: 1em;
    text-align: center;
    background: #ffe1e1;
    display: flex;
    flex-flow: column;
    border-radius: 8px;
    overflow: hidden;
}

.info-grid .card .problem::after {
    content: "";
    display: block;
    height: 12px;
    background: url(../../assets/icons/zigzag.svg);
    background-color: #d4fcea;
}

.info-grid .card .problem-content {
    padding: 1em;
    display: flex;
    flex-flow: column;
    height: 100%;
}

.info-grid .card .solution {
    background: #d4fcea;
    z-index: 1;
    padding: 1em;
    display: flex;
    flex-flow: column;
    flex-grow: 1;
}

.info-grid .card h3 {
    text-align: left;
    font-size: 1.5em;
    margin: 0.5em 0;
}

.info-grid .card p {
    text-align: left;
}

.problem {
    display: flex;
    flex-flow: column;
    flex-grow: 1;
}

.study-case-detail .section.postits .section-content {
    width: 70vw;
}

.study-case-detail .section.postits.qty-2 .section-content {
    width: 50vw;
}

.section.postits.qty-2 .section-content .info-grid {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(1, 1fr);
}

.study-case-detail .section.postits h2 {
    width: 50vw;
    justify-self: center;
    margin-bottom: 2em;
    text-align: center;
}

.study-case-detail ul.content-list {
    padding: revert;
    box-sizing: border-box;
    font-size: 1.1em;
    color: var(--gray-2);
    line-height: 1.5em;
}

.slick-prev:before,
.slick-next:before {
    color: var(--gray-2) !important;
}

.problem-solution-slider {
    max-width: 460px;
}

.column-grid-2 {
    display: flex;
    flex-flow: row;
    justify-content: center;
}

.column-grid-2 .col {
    width: 50%;
}

.col.padding-top {
    padding-top: 10em;
}

.before-after-slider img {
    width: 100%;
}

.section.study-case-header {
    flex-flow: column;
    background: url(../../assets/study-cases/nextchange/header-cover.jpg);
    height: 70vh;
    background-position: 0 -170px;
    width: 100%;
    box-sizing: border-box;
    align-items: center;
    justify-content: flex-end;
}

.section.study-case-header.edden {
    background: url(../../assets/study-cases/edden/cover.jpg);
}

.section.study-case-header.mesirve {
    background: url(../../assets/study-cases/mesirve/Header.png);
}

.study-case-header .section-content h1 {
    color: white;
    font-size: 3.5em;
    line-height: 1.2em;
    padding-right: 3em;
}

.study-case-header p {
    color: white;
}

.drafts-slider img {
    width: 100%;
}

.section-content img {
    width: 100%;
}

.slick-slider {
    margin-bottom: 4em;
}

.study-case-detail p.epigraph {
    font-size: 1em;
    font-style: italic;
}

.section-content strong {
    color: var(--accent-color);
    font-weight: 900;
}

.first-quote img {
    width: 24px;
    margin: 2em 0
}

.first-qoute img {
    width: 64px;
}

.quote-slider {
    width: 100%;
    margin: 0 auto 3em auto !important;
    max-width: 580px;
}

.quote-container {
    display: flex;
    justify-content: center;
    margin: 3em 0;
}

.quote {
    display: flex;
    gap: 24px;
}

.quote-content p {
    font-size: 1.3em;
    margin-top: 8px;
}

p.author {
    font-size: 1.2em;
    font-style: italic;
}

.image-description {
    width: 50%;
    margin: 0 auto;
}

.hifi-slider .hifi-slider-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
}

.hifi-slider img {
    max-width: 960px;
    max-height: 540px;
    margin: 0 0 0 0;
    width: auto;
}

.hifi-slider p.epigraph {
    text-align: center;
}

.metrics-container {
    display: flex;
    flex-flow: row;
    width: 100%;
    margin: 5em 0;
}

.metric-col {
    width: 33%;
    text-align: center;
}

.metrics-container p {
    font-size: 0.9em;
    margin: 0;
    line-height: 1.4em;
}

.metrics-container h3 {
    margin: 0;
    font-size: 3.5em;
    color: var(--primary-color);
}

img.styled-img {
    border-radius: 16px;
    box-shadow: 0px 2px 8px 1px #d2d2d2;
}

.middle-size-img {
    width: 100%;
    max-width: 740px;
    border-radius: 16px;
    margin: 4em auto;
    align-self: center;
    display: block;
}

.hifi-slider.mesirve-slider img {
    margin: 0;
}

.study-case-detail .content {
    padding: 0;
}

.study-case-detail .desktop .hifi-slider-container img,
.study-case-detail .desktop-wireframes .hifi-slider-container img {
    width: 100%;
    height: auto;
    max-height: max-content;
    max-width: max-content;
    margin: 0;
}

@media (max-width: 1440px) {
    .section.study-case-header {
        height: 90vh;
    }
}

@media (max-width: 1366px) {
    .study-case-header .section-content h1 {
        font-size: 3.5em;
        padding-right: 0em;
    }

    .column-grid-2 {
        flex-flow: column;
        align-items: center;
    }

    .image-description {
        width: 100%;
        ;
    }
}

@media (max-width: 768px) {
    .section.study-case-header {
        height: auto;
        padding: 5em 0;
        margin-top: 76px;
    }

    .study-case-header .section-content h1 {
        font-size: 2.4em;
    }

    .study-case-detail .section .section-content {
        width: 80vw;
    }

    .column-grid-2 .col {
        width: 100%;
    }

    .problem-solution-slider {
        max-width: 100%;
    }

    .study-case-detail .row {
        flex-flow: column;
        ;
    }

    .section.study-case-header.mesirve {
        background-position: -659px 531px;
    }
}

@media (max-width: 480px) {
    .study-case-detail .section {
        padding: 2em 3em;
    }

    .section.study-case-header {
        background-position: -280px -320px;
    }

    .quote {
        flex-flow: column;
    }

    .metrics-container {
        flex-flow: column;
        align-items: center;
        gap: 32px;
    }
}