.skill-band {
    position: relative;
    /*width: calc(100vw - 20px); */
    box-sizing: border-box;
    height: 100vh;
    overflow: hidden;
    overflow-x: hidden;
    margin: 128px 0 0 0;
    display: flex;
}

.skill-band-content {
    position: relative;
    top: 20%;
    display: flex;
    width: 100vw;
    height: 60vh;
    justify-content: center;
    flex-flow: row;
}

img.background-img {
    height: 60vh;
    position: absolute;
    top: 20%;
}

.skill-band-content .pictures {
    position: relative;
    width: 50%;
}

img.mid-img1 {
    position: absolute;
    bottom: -16px;
    width: 25vw;
    right: 60px;
}

img.front-img1 {
    position: absolute;
    bottom: 0;
    width: 7vw;
    right: 550px;
    display: none;
}

.text {
    display: flex;
    width: 50%;
    flex-flow: column;
    justify-content: center;
    padding-right: 25%;
    box-sizing: border-box;
}

.text h3 {
    margin: var(--md-spacing) 0;
    font-size: 2.4em;
    font-family: 'Nexa', 'Merriweather Sans';
    font-weight: 100;
    line-height: 1.2em;
}

.text h3 b {
    color: var(--primary-color);
}

.text p {
    font-family: "Merriweather";
    font-size: 1.1em;
    color: #3f3f3f;
    font-weight: 100;
    line-height: 30px;
    letter-spacing: 0.02em;
    font-weight: 100;
}

.text p b {
    font-weight: 700;
    color: var(--accent-color);
}

img.mid-img2 {
    position: absolute;
    bottom: -16px;
    width: 15vw;
    right: 220px;
}

img.front-img2 {
    position: absolute;
    bottom: 0;
    width: 17vw;
    right: -220px;
}

img.mid-img3 {
    position: absolute;
    bottom: -16px;
    width: 25vw;
    right: -240px;
}

img.front-img3 {
    position: absolute;
    bottom: 0;
    width: 14vw;
    right: -20px;
    display: none;
}

.title h3 {
    width: 50vw;
}

.title {
    display: flex;
    justify-content: center;
}